import React, { useState } from "react";
import Modal from "./Modal";
import classes from "./Cart.module.css";
import CartItem from "./CartItem";
import CheckoutForm from "./CheckoutForm";
import useHttp from "../../hooks/use-Http";
import { useSelector, useDispatch } from "react-redux";
import { CartSliceActions } from "../../store/cart-slice/CartSlice";

const Cart = (props) => {
  const [checkoutIsVisible, setCheckoutIsVisible] = useState(false);
  const [orderProcessed, setOrderProcessed] = useState(false);
  const dispatch = useDispatch();
  const total = useSelector((state) => state.cart.total);
  const items = useSelector((state) => state.cart.items);
  const { sendRequest } = useHttp();

  const totalAmount = new Intl.NumberFormat(navigator.language, {
    style: `currency`,
    currency: `GBP`,
  }).format(total.toFixed(2));

  const hideCartHandler = () => {
    dispatch(CartSliceActions.hideCart());
  };

  const hasItems = items.length > 0;

  const showCheckoutHandler = () => {
    setCheckoutIsVisible(true);
  };

  const userDataHandler = (data) => {
    const order = {
      url: `https://react-tester-9d1b5-default-rtdb.europe-west1.firebasedatabase.app/orders.json`,
      method: `POST`,
      headers: {
        "content-Type": "application/json",
      },
      body: { user: data, orderItems: items },
    };

    const orderDataReturn = () => {
      setOrderProcessed(true);
      dispatch(CartSliceActions.clearCart());
    };
    sendRequest(order, orderDataReturn);
  };

  return (
    <Modal onHideCart={hideCartHandler}>
      {orderProcessed && (
        <section className={classes.order}>
          <p style={{ color: `var(--color-golden)` }} className="p-cormorant">
            Thank You! Your order has been confirmed.
          </p>
          <button
            onClick={hideCartHandler}
            className={`custom-button ${classes[`button-orderSuccess`]}`}
            style={{ fontFamily: `var(--font-alt)` }}
          >
            Close
          </button>
        </section>
      )}
      {!orderProcessed && (
        <ul className={classes[`cart-items`]}>
          {items.map((item) => {
            return (
              <CartItem
                key={item.id}
                title={item.title}
                price={item.price}
                quantity={item.quantity}
                item={item}
              />
            );
          })}
        </ul>
      )}
      {!orderProcessed && (
        <div className={classes.total}>
          <span>Total Amount</span>
          <span>{totalAmount}</span>
        </div>
      )}
      <br />
      {checkoutIsVisible && !orderProcessed && (
        <CheckoutForm onClick={hideCartHandler} onUserData={userDataHandler} />
      )}
      <div className={checkoutIsVisible ? classes.hidden : classes.actions}>
        <button
          onClick={hideCartHandler}
          className={`custom-button ${classes[`button--alt`]}`}
          style={{ fontFamily: `var(--font-alt)` }}
        >
          Close
        </button>
        {hasItems && (
          <button
            className={`custom-button ${classes.button}`}
            onClick={showCheckoutHandler}
            style={{ fontFamily: `var(--font-alt)` }}
          >
            Order
          </button>
        )}
      </div>
    </Modal>
  );
};
export default Cart;
