import { useState, useEffect } from "react";
import classes from "./CheckoutForm.module.css";
import useInput from "../../hooks/use-input";

const CheckoutForm = (props) => {
  const {
    value: enteredFullName,
    error: enteredFullNameError,
    isValid: enteredFullNameIsValid,
    inputHandler: fullNameChangeHandler,
    blurHandler: fullNameBlurHandler,
    clear: fullNameClear,
  } = useInput((value) => value.trim() !== `` && value.trim().includes(` `));
  const {
    value: enteredEmail,
    error: enteredEmailError,
    isValid: enteredEmailIsValid,
    inputHandler: emailChangeHandler,
    blurHandler: emailBlurHandler,
    clear: emailClear,
  } = useInput((value) =>
    value
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      )
  );

  const {
    value: enteredMobile,
    error: enteredMobileError,
    isValid: enteredMobileIsValid,
    inputHandler: mobileChangeHandler,
    blurHandler: mobileBlurHandler,
    clear: mobileClear,
  } = useInput((value) => value.trim().length === 11);
  const {
    value: enteredAddress,
    error: enteredAddressError,
    isValid: enteredAddressIsValid,
    inputHandler: addressChangeHandler,
    blurHandler: addressBlurHandler,
    clear: addressClear,
  } = useInput((value) => value.trim() !== `` && value.trim().includes(` `));

  const [formIsValid, setFormIsValid] = useState(false);

  useEffect(() => {
    if (
      enteredAddressIsValid &&
      enteredFullNameIsValid &&
      enteredMobileIsValid &&
      enteredEmailIsValid
    ) {
      setFormIsValid(true);
    } else {
      setFormIsValid(false);
    }
  }, [
    enteredAddressIsValid,
    enteredFullNameIsValid,
    enteredMobileIsValid,
    enteredEmailIsValid,
  ]);

  const submitHandler = (event) => {
    event.preventDefault();

    if (!formIsValid) return;

    const userData = {
      fullName: enteredFullName,
      email: enteredEmail,
      mobile: enteredMobile,
      address: enteredAddress,
    };

    props.onUserData(userData);

    fullNameClear();
    emailClear();
    mobileClear();
    addressClear();
  };

  const nameClasses = enteredFullNameError
    ? `${classes.control} ${classes.invalid}`
    : `${classes.control}`;
  const emailClasses = enteredEmailError
    ? `${classes.control} ${classes.invalid}`
    : `${classes.control}`;
  const addressClasses = enteredAddressError
    ? `${classes.control} ${classes.invalid}`
    : `${classes.control}`;
  const mobileClasses = enteredMobileError
    ? `${classes.control} ${classes.invalid}`
    : `${classes.control}`;
  return (
    <form onSubmit={submitHandler} className={classes.form}>
      <div className={nameClasses}>
        <label htmlFor="fullName">Full Name</label>
        <input
          value={enteredFullName}
          type="text"
          id="fullName"
          onChange={fullNameChangeHandler}
          onBlur={fullNameBlurHandler}
          placeholder="John Doe"
          required
        />
        {enteredFullNameError && (
          <p className="p-opensans">Please enter your full name.</p>
        )}
      </div>
      <div className={emailClasses}>
        <label htmlFor="emailAddress">E-mail Address</label>
        <input
          value={enteredEmail}
          type="email"
          id="emailAddress"
          onChange={emailChangeHandler}
          onBlur={emailBlurHandler}
          placeholder="johndoe@johndoe.com"
          required
        />
        {enteredEmailError && (
          <p className="p-opensans">Please enter your e-mail</p>
        )}
      </div>
      <div className={mobileClasses}>
        <label htmlFor="telephone">Mobile Number</label>
        <input
          value={enteredMobile}
          type="number"
          id="telephone"
          onChange={mobileChangeHandler}
          onBlur={mobileBlurHandler}
          placeholder="07912345678"
        />
        {enteredMobileError && (
          <p className="p-opensans">Please enter your mobile number</p>
        )}
      </div>
      <div className={addressClasses}>
        <label htmlFor="address">Address</label>
        <input
          value={enteredAddress}
          type="address"
          id="address"
          onChange={addressChangeHandler}
          onBlur={addressBlurHandler}
          placeholder="14 Mozart Road, London, W10 5UE"
          required
        />
        {enteredAddressError && (
          <p className="p-opensans">Please enter your current address</p>
        )}
      </div>
      <div className={classes.actions}>
        <button onClick={props.onClick} className={classes[`button--alt`]}>
          Cancel
        </button>
        <button disabled={!formIsValid}>Submit</button>
      </div>
    </form>
  );
};
export default CheckoutForm;
