import React from "react";
import ReactDOM from "react-dom";
import classes from "./Modal.module.css";
import { useDispatch } from "react-redux";
import { CartSliceActions } from "../../store/cart-slice/CartSlice";

const Backdrop = (props) => {
  const dispatch = useDispatch();

  const hideCartHandler = () => {
    dispatch(CartSliceActions.hideCart());
  };

  return (
    <div onClick={hideCartHandler} className={classes[`modal-backdrop`]}></div>
  );
};

const Overlay = (props) => {
  return (
    <div className={`${classes[`modal-overlay`]} ${classes[`slide-down`]}`}>
      <div className={classes[`modal-overlay_content`]}>{props.children}</div>
    </div>
  );
};

const Modal = (props) => {
  return (
    <React.Fragment>
      {ReactDOM.createPortal(
        <Backdrop />,
        document.getElementById(`backdrop-root`)
      )}
      {ReactDOM.createPortal(
        <Overlay>{props.children}</Overlay>,
        document.getElementById(`backdrop-root`)
      )}
    </React.Fragment>
  );
};

export default Modal;
