import classes from "./CartItem.module.css";
import { useDispatch } from "react-redux";
import { CartSliceActions } from "../../store/cart-slice/CartSlice";

const CartItem = (props) => {
  const { item } = props;
  const dispatch = useDispatch();
  const price = new Intl.NumberFormat(navigator.language, {
    style: `currency`,
    currency: `GBP`,
  }).format(props.price.toFixed(2));

  const addItemHandler = () => {
    dispatch(CartSliceActions.addItem({ ...item, quantity: 1 }));
  };
  const deleteItemHandler = () => {
    dispatch(CartSliceActions.deleteItem(item.id));
  };

  return (
    <li className={classes["cart-item"]}>
      <div>
        <h2 className="p-opensans">{props.title}</h2>
        <div className={classes.summary}>
          <span className={classes.price}>{price}</span>
          <span className={classes.amount}>x {props.quantity}</span>
        </div>
      </div>
      <div className={classes.actions}>
        <button className={classes[`button--alt`]} onClick={deleteItemHandler}>
          -
        </button>
        <button onClick={addItemHandler}>+</button>
      </div>
    </li>
  );
};

export default CartItem;
